import tuiAlert from "@/components/extend/alert/alert"
import tuiButton from "@/components/extend/button/button"
export default {
	components: {
		tuiButton,
		tuiAlert
	},
	data() {
		return {
			show: false,
			maskClosable: false,
			btnColor: "#EB0909",
			color: "#333",
			btnText: "确定"
		}
	},
	methods: {
		showAlert(type) {
			this.btnText = "确定";
			this.btnColor = "#EB0909";
			this.color = "#333";
			this.maskClosable = false;
			switch (type) {
				case 2:
					this.btnColor = "#000";
					break;
				case 3:
					this.btnText = "点击关闭";
					break;
				case 4:
					this.color = "#EB0909";
					break;
				case 5:
					this.maskClosable = true;
					break;
				default:
					break;
			}
			this.show = true
		},
		hideAlert(type) {
			this.show = false
		}
	}
}
